import { Component, OnInit } from '@angular/core';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  name: string;
  username: string;
  email: string;
  password: string;

  constructor(
    private validateService: ValidateService,
    private flashMsg: FlashMessagesService,
    private auth: AuthService,
    private router: Router
  ) { }

  ngOnInit() {
  }

  onRegisterSubmit() {
    const user = {
      name: this.name,
      username: this.username,
      email: this.email,
      password: this.password
    }

    // required validation
    if(!this.validateService.validateRegister(user)) {
      this.flashMsg.show('All fields are required', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    // email validation
    if(!this.validateService.validateEmail(user.email)) {
      this.flashMsg.show('Please provide a valid email', {cssClass: 'alert-danger', timeout: 3000});
      return false;
    }

    //register user
    this.auth.registerUser(user).subscribe(data =>  {
      if(data.success){
        this.flashMsg.show('You are now registered', {cssClass: 'alert-success', timeout: 3000});
        this.router.navigate(['/login']);
      } else {
        this.flashMsg.show('Something went wrong!', {cssClass: 'alert-danger', timeout: 3000});
        this.router.navigate(['/register']);
      }
    });

  }

}



